<template>
  <div>
  <div>页面找不到了 </div>
  <div id="time" style=""></div>
  </div>
</template>
<script>
  let temp = this;
  export default {
    data () {
      return {
        time_delay:50,
      }
    },
    methods:{
      showTime_delay:function() {
        var t = 5; //倒计时的秒数

        function showTime(){

          document.getElementById('time').innerHTML= t +"S后跳转到首页";

          if(t==0){
            window.location.href="/";
          }
          t -= 1;
          if(t>=0){
            setTimeout(()=>{
              showTime();
            },1000);
          }
        }
        showTime();
      }
    },mounted() {
      //404页面 取消历史记录中的404页面
      window.history.go(-1);
      this.showTime_delay();

    }

  }

</script>



